import React from 'react'
import {
  Navbar,
  NavbarBrand,
  Nav,
  NavLink,
  Button,
  Dropdown
} from 'react-bootstrap';
import { FaUser } from 'react-icons/fa';
import { withContext } from '../components/layout';
import UserController from "../controllers/userController";
import '../styles/header.scss';

class Header extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    const { showLoginModal } = this.props;
    const username = UserController.isLoggedIn()
      ? UserController.getToken().username
      : null;

    return (
        <header className="header">
          <div>
            <Navbar variant="dark">
              <NavbarBrand href="/"><span></span> Quaranteam</NavbarBrand>
              <Nav className="ml-auto">
                <NavLink href="/projects">Projects</NavLink>
                <NavLink href="/people">People</NavLink>
                {UserController.isLoggedIn()
                  ? (
                    <Dropdown drop="left">
                      <Dropdown.Toggle variant="outline-light" size="sm" className="user-dropdown">
                        <FaUser />
                      </Dropdown.Toggle>
                      <Dropdown.Menu>
                        <Dropdown.Item href={`/profile/${username}`}>My Profile</Dropdown.Item>
                        <Dropdown.Item href={`/profile/${username}?tab=projects`}>My Projects</Dropdown.Item>
                        <Dropdown.Item href={`/profile/${username}?tab=inbox`}>My Inbox</Dropdown.Item>
                        <Dropdown.Item href={`/projects/create`}>Create Project</Dropdown.Item>
                        <Dropdown.Item onClick={UserController.logOut}>Logout</Dropdown.Item>
                      </Dropdown.Menu>
                    </Dropdown>
                  ) : (
                    <Button
                      className="login-btn"
                      variant="light"
                      onClick={() => showLoginModal()}
                    >
                      Login
                    </Button>
                  )
                }
              </Nav>
            </Navbar>
          </div>
        </header>
    );
  }
}

export default withContext(Header);